:root {
  --loginCardWidth: 300px;
  --loginCardPadding: 1em 2em;

  --loginLogoMarginBottom: 0.5rem;
  --loginLogoWidth: 150px;
  --loginHeaderFontSize: 1rem;

  --loginCardInputWidth: 100%;
}

.login-page{
  margin: var(--content-m);
}

.login-card {
  width: var(--loginCardWidth);
  background: rgba(75, 141, 241, 0.767);
  padding: var(--loginCardPadding);
  border-radius: 10px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top: 2em;
  z-index: 2;
}

.login-card::before {
  content: "";
  position: absolute;
  background: rgba(75, 141, 241, 0.425);
  inset: 0;
  transform: rotate(-6deg);
  border-radius: 10px;
  z-index: -1;
}

.login-card-logo {
  margin-bottom: var(--loginLogoMarginBottom);
}

.login-card-logo img {
  width: var(--loginLogoWidth);
}

.login-card-logo,
.login-card header,
.login-card-footer {
  text-align: center;
}

.login-card a {
  text-decoration: none;
  color: #35339a;
}

.login-card a:hover {
  text-decoration: underline;
}

.login-card-header {
  margin-bottom: 2rem;
}

.login-card-header h1 {
  font-size: var(--loginHeaderFontSize);
  font-weight: 600;
  margin-bottom: 0.3rem;
}

.login-card-header h1 + div {
  font-size: calc(0.9rem * 0.8);
  opacity: 0.8;
}

.login-card-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.login-card-form .form-item {
  position: relative;
}

.login-card-form .form-item .form-item-icon {
  position: absolute;
  top: 0.82rem;
  left: 1.4rem;
  font-size: 2rem;
  opacity: 0.4;
  align-items: center;
}

.login-card-form .checkbox {
  display: flex;
  align-items: center;
  gap: 7px;
}

.login-card-form .form-item-other {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: calc(1em * 0.8);
  margin-bottom: 0.8rem;
}

.login-card-footer {
  margin-top: 1rem;
  font-size: calc(1rem * 0.8);
}

.login-card input[type="text"],
.login-card input[type="password"],
.login-card input[type="email"] {
  border: none;
  outline: none;
  background: rgba(255, 255, 255, 0.3);
  padding: 0.5rem 0.5rem;
  padding-left: calc(1rem * 2.5);
  border-radius: 100px;
  width: var(--loginCardInputWidth);
  transition: background 0.5s;
  font-size: 0.9em;
}

.login-card input:focus {
  background: white;
}

.login-card input[type="checkbox"] {
  width: 13px;
  height: 13px;
  accent-color: black;
}

.login-card-form button {
  background: black;
  color: white;
  padding: 10px 1em;
  border-radius: 100px;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: background 0.5s;
  font-size: 0.7em;
}

.login-card button:hover {
  background-color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
}

.login-card-social {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  text-align: center;
  margin-top: 3rem;
}

.login-card-social > div {
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: calc(1rem * 0.8);
}

.login-card-social-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.login-card-social-btn a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 100px;
  transition: all 0.5s;
}

.login-card-social-btn a:hover {
  background: white;
  transform: scale(1.1);
}

/* responsive   */

@media (max-width: 768px) {
  .login-page{
    margin: 0; 
  }
  

  .login-card {
    width: 280px;
    padding: 2rem;
  }
}

.form-item .form-item-icon-material-symbols-rounded img {
  width: 90%;
  height: 2.7em;
  margin: 6px 7px;
  padding: 13px;
  align-items: center;
}

.form-item-icon-material-symbols-rounded {
  position: absolute;
}
